.header {
  position: fixed;
  top: 0;
  height: 60px;
  width: 100%;
  max-width: 100vw;
  background-color: var(--brand-color);
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid lightgray;
}
.header .brand-title {
  margin-left: 15px;
}
.header .cart-icon {
  margin-right: 15px;
}